.info {
	padding: 63px 0 83px;
	text-align: center;
	&__title {
		color: #0d70b7;
		margin-bottom: 25px; }
	&__description {
		margin: 0 auto;
		max-width: 955px;
		color: #a2a2a2;
		font-size: 24px; }
	&__row {
		margin: 0 -32px;
		display: flex;
		flex-wrap: wrap;
		&:not(:first-child) {
			margin-top: 35px; } }
	&__col {
		max-width: 33.3%;
		flex: 0 0 33.3%;
		padding: 0 32px; }
	&__article {
		text-align: left;
		height: 100%;
		color: $white;
		display: flex;
		flex-flow: row wrap;
		align-content: space-between;
		background-color: #0d70b7; }
	&__img {
		height: 500px;
		background-position: 50% 50%;
		background-size: cover; }
	&__wrap {
		padding: 27px 27px 16px 27px; }
	&__date {
		margin-bottom: 10px;
		font-size: 12px; }
	&__title-sm {
		margin-bottom: 15px;
		line-height: 1.1;
		font-family: $montserrat;
		font-size: 24px;
		font-weight: 600; }
	&__text {
		font-size: 18px;
		line-height: 1.3;
		letter-spacing: -0.01em;
		@include font-smoothing;
		p {
			&:not(:first-child) {
				margin-top: 15px; } } }
	&__author {
		min-width: 100%;
		text-align: right;
		padding: 0 27px 16px; } }
